<template>
  <InfiniteList ref="list" v-slot="slotProps" api="teamManage/waitAuditList" immediateCheck>
    <div v-for="item in slotProps.list" :key="item.id" class="list-item" @click="toDetail(item)">
      <div class="list-item-title flex ac js">
        <CustomImg width="1.33rem" height="1.33rem" :src="item.head_pic" round></CustomImg>
        <div class="item info">
          <div class="nickname flex ac">
            {{item.nickname | longtext}}
            <span class="tag ellipsis">
              {{item.level_name}}
              <img class="diamond" :src="require('@/assets/diamond.png')" />
            </span>
          </div>
          <div class="time">提交时间 {{item.create_time}}</div>
        </div>
        <div class="status">{{item.audit_status}}</div>
      </div>
      <div class="img-list">
        <CustomImg
          class="img-item"
          v-if="item.img1_url"
          width="2.4rem"
          height="2.4rem"
          :src="item.img1_url"
          @click.native.stop="showImg(item.img1_url, item.img2_url, 0)"
        ></CustomImg>
        <CustomImg
          class="img-item"
          v-if="item.img2_url"
          width="2.4rem"
          height="2.4rem"
          :src="item.img2_url"
          @click.native.stop="showImg(item.img1_url, item.img2_url, 1)"
        ></CustomImg>
      </div>
      <div class="flex center ac" v-if="item.audit_status === '待审核'">
        <div class="btn text-center" @click.stop="auditTask(item.id, 2)">审核通过</div>
        <div class="btn text-center" @click.stop="show = true, id = item.id">审核不通过</div>
      </div>
      <div v-if='item.remarks'  class="remarks-box">
         <div class="remarks-text">原因：{{item.remarks}}</div>
      </div>
    </div>
    <van-dialog v-model="show" :showConfirmButton="false">
      <div class="modal flex column">
        <img class="modal-close" :src="require('@/assets/close.png')" alt @click="hideModal" />
        <div class="modal-title text-center">填写不通过原因</div>
        <van-field
          v-model="remarks"
          class="remarks"
          rows="9"
          type="textarea"
          placeholder="（选填）填写不通过原因，不超过20字"
          maxlength="20"
        />
        <div class="btns flex js ac">
          <div class="modal-btn text-center" @click="auditTask('', 3)">确定</div>
          <div class="modal-btn text-center" @click="hideModal">取消</div>
        </div>
      </div>
    </van-dialog>
  </InfiniteList>
</template>

<script>
import Vue from "vue";
import { ImagePreview, Dialog, Field } from "vant";
import { auditTask } from "@/api";
import InfiniteList from "@/components/InfiniteList";

Vue.use(ImagePreview);
Vue.use(Dialog);
Vue.use(Field);

export default {
  components: { InfiniteList },
  data() {
    return {
      show: false, // 弹窗
      remarks: "", // 原因
      id: ""
    };
  },
  methods: {
    // 审核
    auditTask(id, audit_status) {
      const params = {
        id: id || this.id,
        audit_status,
        remarks: this.remarks
      };
      auditTask(params).then(res => {
        if (res.success) {
          this.hideModal();
          this.$toast.success("审核成功");
          this.$refs.list.resetData();
        }
      });
    },
    // 查看图片
    showImg(img1, img2, index) {
      const imgs = [img1, img2].filter(item => item);
      ImagePreview({
        images: imgs,
        startPosition: index
      });
    },
    // modal取消
    hideModal() {
      this.show = false;
      this.remarks = "";
    },
    // 详情
    toDetail(data) {
      this.$router.push(`/task/detail?data=${JSON.stringify(data)}`);
    }
  }
};
</script>

<style lang="less" scoped>
.list-item {
  box-sizing: border-box;
  width: 349px;
  background: rgba(255, 255, 255, 1);
  border-radius: 8px;
  margin: 10px auto;
  padding: 10px 10px 15px;
}

.info {
  padding: 0 10px;
}

.nickname {
  color: #282828;
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 6px;
}

.tag {
  box-sizing: border-box;
  max-width: 90.5px;
  margin-left: 8px;
  font-size: 12px;
  font-weight: 500;
  display: inline-block;
  padding: 1px 5px 1px 17px;
  background: rgba(255, 197, 2, 1);
  border-radius: 30px 30px 30px 30px;
  position: relative;
  line-height: 1;
  img {
    width: 17px;
    position: absolute;
    top: 50%;
    left: -1px;
    transform: translateY(-50%);
  }
}

.time {
  color: #666;
  font-size: 12px;
  line-height: 17px;
}

.status {
  color: #e84114;
  font-size: 14px;
}

.img-list {
  padding: 15px 25px;
}

.img-item {
  margin-right: 10px;
}

.btn {
  width: 110px;
  height: 37px;
  line-height: 37px;
  border-radius: 50px 0px 0px 50px;
  border: 1px solid rgba(64, 59, 57, 1);
  color: #282828;
  font-size: 14px;
  &:nth-child(2) {
    border-radius: 0px 50px 50px 0px;
    border-left: none;
    color: #e84114;
  }
}

.modal {
  position: relative;
  box-sizing: border-box;
  width: 315px;
  background: rgba(245, 245, 245, 1);
  border-radius: 11px;
  padding: 20px 15px;
}

.remarks {
  margin: 15px 0 20px;
}

.modal-close {
  position: absolute;
  width: 13px;
  top: 15px;
  right: 15px;
}

.modal-title {
  color: #282828;
  font-size: 16px;
  line-height: 23px;
}

.modal-btn {
  width: 138px;
  height: 44px;
  box-sizing: border-box;
  border-radius: 2px;
  line-height: 44px;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  &:first-child {
    background: rgba(232, 65, 20, 1);
  }
  &:last-child {
    background: #fff;
    color: #282828;
    border: 1px solid rgba(102, 102, 102, 1);
  }
}

.remarks-text{
  line-height: 17px;
  padding: 10px;
  margin: 15px 0;
  background:rgba(245,245,245,1);
  color: #282828;
  font-size: 12px;
}

.remarks-box{
  border-top: 1px solid #D8D8D8
}
</style>