<template>
  <van-list
    v-model="loading"
    :finished="finished"
    :immediate-check="immediateCheck"
    :finished-text="finishedText"
    error-text="请求失败，点击重新加载"
    :error.sync="error"
    @load="getData"
  >
    <div>
      <slot :list="list"></slot>
    </div>
  </van-list>
</template>

<script>
import Vue from "vue";
import { List } from "vant";
import { getList } from "@/api";

Vue.use(List);

export default {
  props: {
    immediateCheck: {
      // 是否在初始化时立即执行滚动位置检查, true会立即请求数据
      type: Boolean,
      default: false
    },
    api: {
      // 列表api接口
      type: String,
      default: "",
      required: true
    },
    params: {
      // 列表请求参数  不需要分页参数page
      type: Object,
      default: () => {}
    },
    formatData: {
      // 格式化列表返回，需要把分页相关参数返回
      type: Function,
      default: res => {
        const { current_page, last_page, data } = res;
        return {
          current_page,
          last_page,
          data
        };
      }
    }
  },
  data() {
    return {
      page: this.immediateCheck ? 0 : 1,
      list: [], // 列表
      loading: false, // 列表loading
      finished: false, // 列表到底了
      error: false // 请求出错
    };
  },
  computed: {
    finishedText() {
      // 列表底部文案
      return this.page === 1 && !this.list.length ? "暂无数据~" : "已经到底了~";
    }
  },
  methods: {
    // 数据
    getData(loadMore = true) {
      this.loading = true;
      const params = {
        ...this.params,
        page: loadMore ? this.page + 1 : this.page
      };
      getList(this.api, params, false)
        .then(res => {
          this.loading = false;
          if (res.success) {
            this.error = false;
            const pageData = this.formatData(res.data);
            const { data, current_page, last_page } = pageData;
            this.page = loadMore ? this.page + 1 : this.page;
            this.list = loadMore ? this.list.concat(data) : data;
            this.finished = !last_page || current_page === last_page;
          } else {
            this.error = true;
          }
        })
        .catch(() => {
          this.loading = false;
          this.error = true;
        });
    },
    // 重置分页
    resetData() {
      this.$nextTick().then(() => {
        this.page = 1;
        this.getData(false);
      });
    }
  }
};
</script>